import axios from 'axios';
window.axios = axios;

window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

window.getAlert = function (type, label, message){
    let ev = new CustomEvent("modal-show", {
        detail: {
            open: true,
            type: type,
            label: label,
            message: message,
        },
        bubbles: true,
        cancelable: false,
        composed: true,
    });
    document.dispatchEvent(ev);
}

window.exceptionsHandler = function(error){
    let response = error.response??{};
    let status = response.status??0;
    switch (status){
        case 413:
            window.getAlert('error', '', '   ,     ')
            break;
        default:
            response.statusText
                ? window.getAlert('error', ' ' + status, response.statusText)
                : window.getAlert('error', ' ' + status, error);
            break;
    }
}

window.downloadHandler = function(name, filepath){
    var link = document.createElement("a");
    link.download = name;
    link.href = filepath + '?v=' + Math.random();
    link.click();
    link.remove();
}

